.contacts{
    background-color: rgba(13, 15, 13, 0.897);
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 0;
    transition: height 300ms;



    >header{
        display: flex;
        border-bottom: solid 1px gray;
        >div{
            flex: 1;
            display: flex;
            padding: 10px 0 10px 10px;
            em{
                align-self: center;
                margin-left: 20px;
                word-wrap: break-word;
            }
            h2{
                align-self: center;
            }
            .loading{
                color: greenyellow;
            }
        }


    }

    >em{
       padding: 10px 0 10px 0;
       width: 100%;
       text-align: center;
       background-color: rgb(26, 26, 78);
    }
    >section{
        flex: 1;
        overflow: auto;
        display: flex;
        flex-direction: column;



        .contact{
            border-bottom: solid grey 1px;
            padding-left: 10px;
            word-wrap: break-word;
        }

        .contact-selected{
            color: greenyellow;
        }

    }

    >.disabled{
        color: gray;
    }

}

